namespace Loads2019.Types

type Line = { 
    Name : string
    Samples : int list 
    }

module Line =
    let create name samples =
        { Name = name; Samples = samples }
        
type Section = {
    Lines : Line list 
}

module Section =
    let create lines =
        { Lines = lines }

type Station = {
    Name : string
    Sections : Section list
}

module Station =
    let create name sections =
        { Name = name; Sections = sections }