module Loads2019.Calc 

open Loads2019.Types

//----------------------------------------------------------------------------//
// Utils 
//----------------------------------------------------------------------------//

let private isOutSample sample =
    sample <= 0

let private isInSample sample =
    sample > 0

let private isAllOutSamplesOnList samples =
    List.forall isOutSample samples

let private isAllInSamplesOnList samples =
    List.forall isInSample samples

let rec private transpose = function
    | (_::_)::_ as M -> List.map List.head M :: transpose (List.map List.tail M)
    | _ -> []

//----------------------------------------------------------------------------//
// Lines 
//----------------------------------------------------------------------------//

module Line =

    let recalcOutSamples f (line : Line) =
        match line with
        | { Line.Samples = samples } when isAllOutSamplesOnList samples -> 
            { line with Samples = List.map f samples }
        | _ -> line

    let recalcInSamples f values (line : Line) =
        match line with
        | { Line.Samples = samples } when isAllInSamplesOnList samples ->
            let f' sample value =
                if value = 0 && sample <> 0
                then f sample
                else value
            in { line with Samples = List.map2 f' samples values}
        | _ -> line

//----------------------------------------------------------------------------//
// Sections 
//----------------------------------------------------------------------------//

module Section =

    let private transposeSamples (section : Section) =
        section.Lines
        |> List.map (fun i -> i.Samples)
        |> transpose

    let private sumOutSamples =
        transposeSamples
        >> List.map (List.filter isOutSample 
                     >> List.sum
                     >> abs)

    let private countInSamples =
        transposeSamples
        >> List.map (List.filter isInSample
                     >> List.length)

    // Can throw ZeroDivision exception
    let private calcLoads section =
        let loadOnSection = sumOutSamples section
        let inLinesNum = countInSamples section
        List.map2 (fun load len -> int(round(float load / float len))) loadOnSection inLinesNum 

    let recalcOutSamples f (section : Section) =
        let lines = section.Lines
        { section with Lines = List.map (Line.recalcOutSamples f) lines}

    let recalcInSamples f (section : Section) =
        let lines = section.Lines
        let loadsOnSection = calcLoads section
        { section with Lines = List.map (Line.recalcInSamples f loadsOnSection) lines}

    let recalcSamples f =
        recalcOutSamples f 
        >> recalcInSamples f

//----------------------------------------------------------------------------//
// Stations 
//----------------------------------------------------------------------------//

module Station =

    let private mapUpdateSections f (station : Station) =
        { station with Sections = List.map f station.Sections}

    let recalcOutSamples f (station : Station) =
        mapUpdateSections (Section.recalcOutSamples f) station

    let recalcInSamples f (station : Station) =
        mapUpdateSections (Section.recalcInSamples f) station

    let recalcSamples f (station : Station) =
        mapUpdateSections (Section.recalcSamples f) station