module Loads2019.Fake

open Loads2019

let private limitRange = [ 
    (100, (5, 25))
    (50, (5, 20))
    (15, (5, 10))
    (5, (0, 5))
    ]

let rec private getMod2or5 value =
    if value % 2 = 0 || value % 5 = 0 
    then value
    else getMod2or5 (value + 1)

let private rangeValue value minRand maxRand =
    let r = System.Random()
    let minRand = r.Next(minRand)
    let maxRand = r.Next(maxRand)
    if value < 0
    then minRand + (value - maxRand)
    else maxRand + (value - minRand)

let recalcLoad value =
    let rec recalcLoad' value limits =
        match limits with
        | [] -> value
        | ((limit, (minR, maxR)) :: tail) ->
            if abs value >= limit
            then rangeValue value minR maxR |> getMod2or5
            else recalcLoad' value tail 
    recalcLoad' value limitRange

module Line =
    open Types.Line
    open Calc.Line

    let fakeInSamples values =
        recalcInSamples recalcLoad values 

    let fakeOutSamples =
        recalcOutSamples recalcLoad 
        
module Section =
    open Types.Section
    open Calc.Section

    let fakeInSamples =
        recalcInSamples recalcLoad 

    let fakeOutSamples =
        recalcOutSamples recalcLoad 
        
    let fakeSamples =
        recalcSamples recalcLoad 
        
module Station =
    open Types.Station
    open Calc.Station

    let fakeInSamples =
        recalcInSamples recalcLoad 

    let fakeOutSamples =
        recalcOutSamples recalcLoad 
        
    let fakeSamples =
        recalcSamples recalcLoad 
        